<template>
  <div class="row" v-if="!currentArticle">
    <!-- Carousel Article -->
    <div id="ArticleHome" class="carousel slide" data-bs-ride="carousel">
      <!-- Indicators/dots -->
      <div class="carousel-indicators">
        <button type="button" data-bs-target="#ArticleHome" data-bs-slide-to="0" class="active"></button>
        <button type="button" data-bs-target="#ArticleHome" data-bs-slide-to="1"></button>
        <button type="button" data-bs-target="#ArticleHome" data-bs-slide-to="2"></button>
      </div>

      <!-- The slideshow/carousel -->
      <div class="carousel-inner">
        <div class="carousel-item "  :class="{ 'active' : index === 0 }" v-for="(articleImage, index) in articles"  :key="index">
          <img src="image-home.png" alt="Los Angeles" class="d-block w-100">
        </div>
      </div>

      <!-- Left and right controls/icons -->
      <button class="carousel-control-prev" type="button" data-bs-target="#ArticleHome" data-bs-slide="prev">
        <span class="carousel-control-prev-icon"></span>
      </button>
      <button class="carousel-control-next" type="button" data-bs-target="#ArticleHome" data-bs-slide="next">
        <span class="carousel-control-next-icon"></span>
      </button>
    </div>
    <div class="container text-left">
        <div class="row article-list">
          <div class="col-md-12 m-3 text-center"><h1>Article</h1></div>
          <div class="col-md-4"  v-for="(article, index) in articles"  :key="index">
            <img class="card-img-top" src="https://www.w3schools.com/bootstrap5/img_avatar1.png" alt="Card image">
            <h2 @click="showDetail(article, index)">{{ article.title }}</h2>
            <div class="mt-5" style="overflow:auto" >
            {{ article.short_description }}
            </div>
          </div>
          
        </div>
    </div>
  </div>  
  <div class="container row p-md-3" v-if="currentArticle">
    <div class="col-12"  >
      <h1>{{currentArticle.title}}</h1>
      <p>{{currentArticle.short_description}}</p>
      <img src="image-home.png" class="img img-fluid" >
      <p v-html="currentArticle.description"></p>
    </div>
    <div class="col-12 text-right">
      <button class="btn btn-outline-warning mb-0 ml-auto" @click="ShowAllArticle">Kembali</button>
    </div>
  </div>
</template>

<script>
import ArticleDataService from "../services/ArticleDataService";

export default {
  name: "articles-list",
  data() {
    return {
      articles: [],
      currentArticle: null,
      currentIndex: -1,
      title: ""
    };
  },
  methods: {
    retrieveArticles() {
      ArticleDataService.getNew()
        .then(response => {
          this.articles = response.data;
          console.log(response.data);
        })
        .catch(e => {
          console.log(e);
        });
    },

    refreshList() {
      this.retrieveArticles();
      this.currentArticle = null;
      this.currentIndex = -1;
    },

    showDetail(article, index) {
      this.currentArticle = article;
      this.currentIndex = article ? index : -1;
    },
    ShowAllArticle(){
      this.currentArticle = null;
    }

    
  },
  mounted() {
    this.retrieveArticles();
  }
};
</script>

<style>
.list {
  text-align: left;
  max-width: 750px;
  margin: auto;
}
</style>
